@import url(https://fontlibrary.org//face/8bit-wonder);
:root {
  font-size: 16px;
  font-family: "8BITWONDERNominal", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: whitesmoke;
}
body::-webkit-scrollbar {
  width: 0.25rem;
}
body::-webkit-scrollbar-track {
  background: #1b531d;
}
body::-webkit-scrollbar-thumb {
  background: #6d6525;
}

.navBar {
  width: 12rem;
  height: 100vh;
  position: fixed;
  background-color: #b3936b;
  transition: width 200ms ease;
  overflow: hidden;
}
.navBar .logo {
  font-size: 1rem;
  font-weight: bold;
  color: #184e24;
  margin-top: 1rem;
}
.navBar .navBar-nav {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.navBar .navBar-nav .navItem {
  height: 9rem;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.navBar .navBar-nav .navItem:last-child {
  margin-bottom: 5rem;
  margin-top: auto;
}
.navBar .navBar-nav .navItem:last-child li p {
  text-justify: newspaper;
}
.navBar .navBar-nav .navItem .gridControls {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  height: 40px;
  text-decoration: none;
}
.navBar .navBar-nav .navItem .gridControls input {
  height: 100%;
  width: 2.5rem;
  background-color: #b49b7d;
  color: whitesmoke;
  font-size: 1.5rem;
  text-align: center;
}
.navBar .navBar-nav .navItem .gridControls ::-webkit-outer-spin-button,
.navBar .navBar-nav .navItem .gridControls ::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.playArea {
  margin-left: 12rem;
  height: 100vh;
}
.playArea .playArea-grid {
  height: 100%;
  width: calc(100vw - 12rem);
  table-layout: fixed;
  border-collapse: collapse;
}
.playArea .playArea-grid td {
  padding: 0.5px;
  background-color: #1d5d91;
}
.playArea .playArea-grid td img {
  width: 100%;
  height: 100%;
}

